import grapesjs from 'grapesjs';
const loadCss = editor => {
  editor.Config.canvasCss += `.center{ text-align:center }`
}
export default grapesjs.plugins.add('gjs-blocks-basic', (editor, opts = {}) => {
  const config = {
    blocks: ['Headings', 'Features', 'Footer', 'Testimonials', 'Navigation', 'Buttons', 'Teams', 'Pricing', 'Intros', 'Forms'],
    flexGrid: 0,
    stylePrefix: 'gjs-',
    addBasicStyle: true,
    category: 'Basic',
    categoryNavigation: 'Navigation',
    categoryHeadings: 'Headings',
    categoryFeatures: ' Features',
    categoryFooter: 'Footer',
    categoryIntros: 'Intros',
    categoryButtons: 'Buttons',
    categoryPricing: 'Pricing',
    categoryTeams: 'Teams',
    categoryTestimonials: 'Testimonials',
    categoryForms: 'Forms',
    ...opts
  };

  // Add blocks
  const loadBlocks = require('./blocks');
  loadBlocks.default(editor, config);

});
